/**
 * –§–æ—Ä–º–∞—Ç –¥–ª—è –æ–±—ä–µ–∫—Ç–∞ –∫—Ä–µ—Å—Ç–∏–∫–∏-–Ω–æ–ª–∏–∫–∏
 * [[{name: "cross"|"zero"|null, winner: boolean},{},{}],[{},{},{}],[{},{},{}]]
 */

export class TictactoeStore {
    size = 3;
    gameStarted = false;
    whoWalkNow = null;

    // —Å–æ–∑–¥–∞–µ—Ç –ø—É—Å—Ç–æ–π –¥–≤—É–º–µ—Ä–Ω—ã–π –º–∞—Å—Å–∏–≤ —Å –æ–±—ä–µ–∫—Ç–∞–º–∏ - –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∏–ª–∏ –æ–±–Ω—É–ª–µ–Ω–∏–µ
    getNullData = () => {
        let data = [];
        if (!this.size) return;
        for (let i = 0; i < this.size; i++) {
              data[i] = [];
            for (let j = 0; j < this.size; j++) {
              data[i][j] = {};
            }
        }
        return data;
    }

    // —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –≤ –∫–ª–µ—Ç–∫—É –∫–ª–æ–Ω–∞ –º–∞—Å—Å–∏–≤–∞ –∫—Ä–µ—Å—Ç–∏–∫ –∏–ª–∏ –Ω–æ–ª–∏–∫, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤
    handlerStroke = (rIndex, cIndex, data) => {
        console.log('data', data);
        let newData = deepClone(data);
        if (!this.gameStarted) {
            this.gameStarted = true;
            this.whoWalkNow = 'cross';
        } else {
            this.whoWalkNow = this.whoWalkNow === 'cross' ? 'zero' : 'cross';
        }
        newData[rIndex][cIndex].name = this.whoWalkNow;
        
        return newData;
    }

    //–ø—Ä–æ–≤–µ—Ä—è–µ—Ç —á—Ç–æ —Å–≤–æ–±–æ–¥–Ω—ã—Ö –∫–ª–µ—Ç–æ–∫ –±–æ–ª—å—à–µ –Ω–µ—Ç - –∑–∞–∫–æ–Ω—á–∏–ª–∏—Å—å —Ö–æ–¥—ã
    deadHeat = (data) => {
        if (!data.length) return false;
        for (let i = 0; i < data.length; i++) {
            let row = data[i];
            for (let j = 0; j < row.length; j++) {
                if (!row[j].name) {
                    return false
                };
            }
        }
        return true;
    }

    //–ø—Ä–æ–≤–µ—Ä—è–µ—Ç –µ—Å—Ç—å –ª–∏ –ø–æ–±–µ–¥–∏—Ç–µ–ª—å fixme
    isVictory = (data) => {
        let victory = false;
        if (!data.length) return false;
        //–ó–∞–ø–æ–ª–Ω–µ–Ω–∞ –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ª–µ–≤–∞ –Ω–∞–ø—Ä–∞–≤–æ
        let leftDiagonal = [];
        for (let i = 0; i < data.length; i++) {
            leftDiagonal.push(data[i][i]);
        }
        if (this.checkRow(leftDiagonal)) victory = true;
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ø—Ä–∞–≤–∞ –Ω–∞–ª–µ–≤–æ
        let rightDiagonal = [];
        for (let i = 0; i < data.length; i++) {
            rightDiagonal.push(data[data.length-1-i][i]);
        }
        if (this.checkRow(rightDiagonal)) victory = true;
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏
        let verticalData = [[],[],[]];
        data.forEach((row, index)=>{
            // if (victory) return;
            for (let i = 0; i < row.length; i++) {
                verticalData[i][index] = row[i]
            }
        });
        for (let i = 0; i < verticalData.length; i++) {
            if (this.checkRow(verticalData[i])) victory = true;
        }
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏
        for (let i = 0; i < data.length; i++) {
           if (this.checkRow(data[i])) victory = true;
        }
        return victory;
    }

    //–ü—Ä–æ–≤–µ—Ä—è–µ–º —á—Ç–æ –≤ –æ–¥–Ω–æ–º–µ—Ä–Ω–æ–º –º–∞—Å—Å–∏–≤–µ –≤—Å–µ —Å–≤–æ–π—Å—Ç–≤–∞ name –≤–ª–æ–∂–µ–Ω–Ω—ã—Ö –æ–¥–∏–Ω–∞–∫–æ–≤—ã–µ

    checkRow = (data) => {
        if (!data.length) return false;
        let firstElem = data[0].name;
        if (!firstElem) return false;
        for (let i = 0; i < data.length; i++) {
            if (data[i].name !== firstElem) return false;
        }
        return true;
    }

    checkLeftDiagonal = (data) => {
        let firstElem = data[0][0].name;
        if (!firstElem) return false;
        for (let i = 0; i < data.length; i++) {
            if (data[i][i].name !== firstElem) return false;
        }
        return true;
    }

    checkRightDiagonal = (data) => {
        let firstElem = data[data.length-1][0].name;
        if (!firstElem) return false;
        for (let i = 0; i < data.length; i++) {
            if (data[data.length-1-i][i].name !== firstElem) return false;
        }
        return true;
    }

    findWinner–°olumnIndex = (data) => {
        if (!data.length) return -1;

        for (let i = 0; i < data.length; i++) {
            let firstElem = data[0][i].name;
            if (!firstElem) continue;
            let found = true;
            for (let j = 0; j < data.length; j++) {
                if (data[j][i].name !== firstElem) {
                    found = false;
                    break;
                }
            }
            if (found) {
                return i;
            }
        }
        return -1;
    }

    /**
     * –í—ã–ø–æ–ª–Ω—è–µ—Ç –ø—Ä–æ–≤–µ—Ä–∫–∏ –Ω–∞ –ø–æ–±–µ–¥—É 
     * –∏ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –∫–ª–µ—Ç–∫–∞–º —Å–≤–æ–π—Å—Ç–≤–æ winner –≤ true,
     * –µ—Å–ª–∏ –æ–Ω–∏ –Ω–∞—Ö–æ–¥—è—Ç—Å—è –≤ —Ä—è–¥—É –ø–æ–±–µ–¥–∏—Ç–µ–ª—è
     */
    setWinnerStyle = (data) => {
        let newData = deepClone(data); 
        if (!newData.length) return newData;

        let isWinner = false;
        //–ó–∞–ø–æ–ª–Ω–µ–Ω–∞ –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ª–µ–≤–∞ –Ω–∞–ø—Ä–∞–≤–æ

        isWinner = this.checkLeftDiagonal(newData);
        if (isWinner) {
            for (let i = 0; i < newData.length; i++) {
                newData[i][i].winner = true;
            }
            return newData;
        }

        // –ó–∞–ø–æ–ª–Ω–µ–Ω –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ø—Ä–∞–≤–∞ –Ω–∞–ª–µ–≤–æ
        isWinner = this.checkRightDiagonal(newData);
        if (isWinner) {
            for (let i = 0; i < newData.length; i++) {
                newData[data.length-1-i][i].winner = true;
            }
            return newData;
        }

        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏

        for (let i = 0; i < newData.length; i++) {
            isWinner = this.checkRow(newData[i]);
            if (isWinner) {
                for (let j = 0; j < newData[i].length; j++) {
                    newData[i][j].winner = true; 
                }
                return newData;
            }
        }

        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏
        let columnIndex = this.findWinner–°olumnIndex(newData);
        isWinner = columnIndex !== -1 ? true : false;

        if (isWinner) {
            for (let i = 0; i < newData.length; i++) {
                newData[i][columnIndex].winner = true; 
            }
            return newData;
        }
        return newData;
    }

    //–≤—ã–≤–æ–¥–∏—Ç –∏–º—è –ø–æ–±–µ–¥–∏—Ç–µ–ª—è fixme
    findWinner = (data) => {
        let winner = null;
        if (!data.length) return false;
        //–ó–∞–ø–æ–ª–Ω–µ–Ω–∞ –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ª–µ–≤–∞ –Ω–∞–ø—Ä–∞–≤–æ
        let leftDiagonal = [];
        for (let i = 0; i < data.length; i++) {
            leftDiagonal.push(data[i][i]);
        }
        if (this.checkRow(leftDiagonal)) {
            winner = leftDiagonal[0].name;
            return winner;
        };
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ø—Ä–∞–≤–∞ –Ω–∞–ª–µ–≤–æ
        let rightDiagonal = [];
        for (let i = 0; i < data.length; i++) {
            rightDiagonal.push(data[data.length-1-i][i]);
        }
        if (this.checkRow(rightDiagonal)) {
            winner = rightDiagonal[0].name;
            return winner;
        };
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏
        let verticalData = [[],[],[]];
        data.forEach((row, index)=>{
            for (let i = 0; i < row.length; i++) {
                verticalData[i][index] = row[i]
            }
        });
        for (let i = 0; i < verticalData.length; i++) {
            if (this.checkRow(verticalData[i])) {
                winner = verticalData[i][0].name;
                return winner;
            };
        }
    
        // –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏
        for (let i = 0; i < data.length; i++) {
           if (this.checkRow(data[i])) {
            winner = data[i][0].name;
            return winner;
           };
        }
        return winner;
    }
}

function copyArray(arr) {
    if (arr instanceof Array) {
        let copy = [];
        for (let i = 0; i < arr.length; i++) {
            copy[i] = copyArray(arr[i]);
        }
        return copy;
    }
    return arr;
}

export function deepClone(obj) {
    if (obj === null || typeof obj !== "object") {
      return obj;
    }
  
    if (obj instanceof Array && obj.length) {
      let copy = [];
      for (let i = 0; i < obj.length; i++) {
        copy[i] = deepClone(obj[i]);
      }
      return copy;
    }
  
    if (obj instanceof Object) {
      let copy = {};
      for (let key in obj) {
        if (obj.hasOwnProperty(key)) {
          copy[key] = deepClone(obj[key]);
        }
      }
      return copy;
    }
  }
  